.names-form-wrapper {
  width: 300px;
  margin: auto;
}

@media (min-width: 677px) {
  .names-form-wrapper {
    width: 620px;
  }
  .names-form-inner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .names-form-container {
    width: 620px;
  }
  .names-form-container .group-input {
    width: 280px;
  }
  .names-form-container button {
    margin-left: 248px;
    margin-right: 249px
  }
  .names-instructions {
    width: 100%;
    padding: 15px 50px;
  }
}

@media (min-width: 1050px) {
  .names-form-wrapper {
    width: 940px;
  }
  .names-form-container {
    width: 940px;
  }
  .names-form-container .group-input {
    width: 280px;
  }
  .names-form-container button {
    margin-left: 408px;
    margin-right: 409px
  }
  .names-instructions {
    padding: 15px 100px;
  }
}


